import React from "react"

import { useTrail, animated } from "react-spring"
import { Intro } from "../components/intro"
import { Banner } from "../components/banner"
import { Container } from "../components/container"
import { Columns } from "../components/columns"
import { Canberra } from "../components/canberra"
import { Highlight } from "../components/intro/highlight"
import { Color } from "../components/intro/color"
import { SpringFadeUp, TrailFadeUp } from "../utils/animations"
import { colors } from "../styles/variables"

export default () => {
	const trail = useTrail(6, {
		from: {
			opacity: 0,
			transform: "translate(0px, 24px)",
		},
		opacity: 1,
		transform: "translate(0px, 0px)",
	})
	return (
		<>
			<Canberra title="Home" />

			<Banner>
				<h2>
					A
					<br />
					<Highlight>refrigerator,</Highlight><br />
					which doesn't give you<br />
					<Highlight>the cold shoulder.</Highlight> <br />
					
				</h2>
			</Banner>

			<Container>
				<Intro>
					<SpringFadeUp>
						<p>
							Rebridgerator is a bridge between<br/> your{" "}
							<Color>mood</Color> and the{" "}
							<Color>food</Color><br/> you like to consume accordingly.
						</p>
					</SpringFadeUp>
				</Intro>
			</Container>
		</>
	)
}
